import { MenuContext } from '../App';
import { useContext } from 'react';
const Item = (props) => {
  const { ticket, index } = props
  const nameLock = 25;
  const { setIsComponentVisible } = useContext(MenuContext);
  return (
    <div className="ItemConteiner">

      <div className="ItemCode">
        <button className="ItemCode_button" onClick={() => setIsComponentVisible([true, ticket])}>Detail</button>

        <p className="ItemCode_code">KOD: {ticket.qrcode}</p>

      </div>

      <div className="ItemInfo">
        {/* zabezpieczenie przed robieniem z nazwy 2 linijkowego elementu,
        jesli nazwisko jest dłuższe niz 25 znakow ( bo ktos podal debilie 
          email gdzie powinno byc nazwisko) to nie wyswietla sie napis 
          "Name: " przed imieniem co skutkuje skroceniem calego ciagu na tyle
           by wszystko sie wpasowalo w jedna linie :) */}
        {ticket.clientName.length <= nameLock ?
          <p>Name: {ticket.clientName}</p>
          :
          <p>{ticket.clientName}</p>
        }
        <p>
          {!ticket.isValid ?
            "Status: skasowany"
            :
            "Status: Nieskasowany"}
        </p>

      </div>
    </div>);
}

export default Item;