import firebase from 'firebase'
import { MenuContext } from '../App';
import { useContext } from 'react'
import Button from '@material-ui/core/Button'
const Logout = (props) => {

    const { setDisplayed } = useContext(MenuContext);
    const logout = () => {
        firebase.auth().signOut();
        setDisplayed("Tickets")
    };

    return (
        <div className="logout">
            <div className="logout_window">
                <p className="logout_window_text">Are you sure want to sign out?</p>
                <div className="logout_window_buttons">
                    <Button variant="contained"
                        style={{ margin: '0 6vw', width: '13vh', borderRadius: '10px' }}
                        onClick={() => logout()}>
                        Yes!
                    </Button>
                    <Button variant="contained"
                        style={{ margin: '0 6vw', width: '13vh', borderRadius: '10px' }}
                        onClick={() => setDisplayed("Tickets")}>
                        No!
                    </Button>
                </div>
            </div>
        </div>);
}

export default Logout;