import { useAuthState } from 'react-firebase-hooks/auth';
import firebase from 'firebase'
import { MenuContext } from '../App';
import { useContext } from 'react'
import SignIn from './SignIn'
import Scanner from './Scanner'
import AllTickets from './Tickets'
import Logout from './Logout'
import Admin from './admin'



const Main = (props) => {
  const { displayed } = useContext(MenuContext);
  const [user, loading, error] = useAuthState(firebase.auth());
  if (user) {
    switch (displayed) {
      case "Skaner":
        return (
          <>
            <div className="background"></div>
            <div className="main">
              <Scanner />
            </div>
          </>
        );
      case "Tickets":
        return (
          <>
            <div className="background"></div>
            <div className="main">
              {/* {console.log(user)} */}
              <AllTickets />
            </div>
          </>
        );
      case "Logout":
        return (
          <>
            <div className="background"></div>
            <div className="main">
              <Logout />
            </div>
          </>
        );
      case "Admin":
        return (
          <>
            <div className="background"></div>
            <div className="main">
              <Admin user={user} />
            </div>
          </>
        );
      default:
        return (
          <>
            <div className="background"></div>
            <div className="main">
              <p>problem with setting context.</p>
              <p>This part of App is still under development or you dont have permission to view it</p>
            </div>
          </>
        );
    }
  }
  else if (loading) {
    return (
      <>
        <div className="background"></div>
        <div className="main">
          <div className="SignIn">
            <div className="SignIn_section_one">
              <p>Welcome To QR Reader!</p>
              <p>Time 2 Party</p>
              <p>Initialising User...</p>
            </div>
          </div>
        </div>
      </>
    );
  }
  else if (error) {
    return (
      <div className="main">
        <p>Error: {error}</p>
      </div>
    );
  }
  else {
    return (
      <>
        <div className="background"></div>
        <div className="main">
          <SignIn />
        </div>
      </>
    );
  }

}

export default Main;