import { MenuContext } from '../App';
import { useContext } from 'react'
import { timeConverter } from './timeConverter'
import Button from '@material-ui/core/Button'


const TicketDetail = (props) => {
    const { isComponentVisible, setIsComponentVisible } = useContext(MenuContext);

    const handleQuit = () => {
        setIsComponentVisible([false, null])

    }
    return (
        <div className="TicketDetail">
            <div className="TicketDetail_name">
                <div className="TicketDetail_name_box">
                    <p>imie: {isComponentVisible[1].clientName}</p>
                    <p>Kod QR: {isComponentVisible[1].qrcode}</p>
                </div>
            </div>
            <div className="TicketDetail_status">
                <div className="TicketDetail_status_box">
                    <div className="TicketDetail_status_box_col_1">
                        <p> STATUS: <br />{!isComponentVisible[1].isValid ? "Skasowany" : "Nieskasowany"}</p>
                        <p>CZAS: <br />{!isComponentVisible[1].isValid ? timeConverter(isComponentVisible[1].validatedAt) : "not known"}</p>
                    </div>
                    <div className="TicketDetail_status_box_col_2">
                        <p>RODZAJ BILETU:  <br /> Vip</p>
                        <p>DROGA ZAKUPU:  <br />Online</p>
                    </div>

                </div>
            </div>
            <div className="TicketDetail_promo">
                <div className="TicketDetail_promo_box">
                    <p>promocje/dodatki:<br />  2 Shoty <br /> Loża - 5os.</p>
                    przemyslec wyglad i dodac opcje edycji biletow i wysylania na serw
                </div>
            </div>
            <div className="TicketDetail_button">
                <Button variant="contained"
                    style={{ margin: '0.5vh 0', width: '20vw', height: "5vh", borderRadius: '10px' }}
                    onClick={() => handleQuit()}>
                    Close
                </Button>
            </div>

        </div>
    )
}
export default TicketDetail;