export const timeConverter = (timestamp) => {  
    const mili = new Date(timestamp);
    const year = mili.getFullYear();
    const month = mili.getMonth() < 10 ? '0' + (mili.getMonth() + 1) : (mili.getMonth() + 1);
    const day = mili.getDate();
    const hour = mili.getHours() < 10 ? '0' + mili.getHours() : mili.getHours();
    const min = mili.getMinutes() < 10 ? '0' + mili.getMinutes() : mili.getMinutes();
    const sec = mili.getSeconds() < 10? '0' + mili.getSeconds() : mili.getSeconds();
    const time = day + '.' + month + '.' + year + ' - ' + hour + ':' + min + ':' + sec ;
    return time;
  }

