import { useState, useEffect, useRef } from "react";

// component ktory zrobilem do ogarniania kliku poza okno szczegolow 
// klienta ( tickets-detail) przyjmuje argumenty takie jak 1)ref ktore okno
//  ma byc obserwowane do klikania i poza nie 2)isComponentVisible to Usestate 
//  przyjmujacy true albo false w zaleznosci od tego czy okno ma byc widoczne 
//  czy nie oraz przyjmuje obiekt konkretnego ticketu ktorym ma wypelnic okno
//  oraz 3) setIsComponentVisible to wiadomo, ustawia arrey stanu "[true/false , ticket]"

export default function useComponentVisible(initialIsVisible, index) {
    const [isComponentVisible, setIsComponentVisible] =
        useState([initialIsVisible, index]);
    const windowref = useRef(null);

    const handleClickOutside = (event) => {
        if (windowref.current && !windowref.current.contains(event.target)) {
            setIsComponentVisible([false, null]);
        }
    };

    useEffect(() => {
        document.addEventListener("click", handleClickOutside, true);
        return () => {
            document.removeEventListener("click", handleClickOutside, true);
        };
    });

    return { windowref, isComponentVisible, setIsComponentVisible };
}