import { useState, useCallback } from 'react';
import Item from './Item'
import { MenuContext } from '../App';
import { useContext } from 'react'
import TicketDetail from './TicketDetail';

const AllTickets = (props) => {
  const
    { ticketsRef,
      windowref,
      isComponentVisible,
      ticketRendered,
      SetTicketRendered,
      tickets,
      setTickets

    } = useContext(MenuContext);
  const [search, Setsearch] = useState();
  const [TicketType, SetTicketType] = useState("All")


  const fetchRequest = useCallback(() => {
    const fetchData = async () => {
      const data = await ticketsRef.get();
      console.log("render")
      setTickets(data.docs.map(doc => ({ ...doc.data(), id: doc.id })));
    };
    fetchData();
  })


  const searched = (value) => {
    if (search) {
      if (value.clientName.toLowerCase().includes(search.toLowerCase())) {
        return true
      }
    }
    else { return true }
  }

  const isScaned = (value) => {
    if (TicketType === "Sold") {
      return !value.isValid;
    }
    else { return true }
  }


  return (

    <div className="AllTickets">
      <div className="searchbox">
        <div className="searchbox_search">
          <input className="searchbox_search_input" type="search" placeholder="search..." onChange={e => Setsearch(e.target.value)} />
        </div>
        <div className="searchbox_selector">
          <div>
            <label className="searchbox_selector_label">Wszystkie Bilety
              <input type="radio" id="All" name="Tickets_Type" checked={TicketType === "All"} onChange={e => SetTicketType(e.target.id)} />
            </label>
          </div>
          <div>
            <label className="searchbox_selector_label">Skasowane Bilety
              <input type="radio" id="Sold" name="Tickets_Type" onChange={e => SetTicketType(e.target.id)} />
            </label>
          </div>
        </div>
      </div>

      {ticketRendered ?
        tickets.filter(isScaned).filter(searched).map((value, index) => (
          <Item key={index} index={index} ticket={value} />
        ))
        :
        <div className="temp_warning">
          <p>WARNING!</p>
          <p>this part of App is in experimental state and may causes problems with the server</p>
          <p>Do not! render tickets detail if you dont need to use it!</p>
          <button onClick={() => (SetTicketRendered(true), fetchRequest())}>render anyway!</button>
        </div>
      }
      {isComponentVisible[0] ?
        <div className="popup" ref={windowref}><TicketDetail /></div>
        : null}

    </div>
  );
}

export default AllTickets;