import { useState, useContext } from 'react';
import QrReader from 'react-qr-reader'
import Button from '@material-ui/core/Button';
import useSound from 'use-sound';
import scan from '../scan.mp3';
import { MenuContext } from '../App';
import error from '../error.mp3';

function Scanner() {
  const { ticketsRef } = useContext(MenuContext)
  const [delay, setDelay] = useState(false)
  const [result, setResult] = useState(null)
  const [scanInfo, SetScanInfo] = useState([false, ""])
  const [Client, setClient] = useState("data")
  const [ticketUpdate, setTicketUpdate] = useState("")
  const [scanerMode, setScanerMode] = useState("tickets")
  let [counter, setCounter] = useState(0)
  // =======================================================
  const updateTicket = async (ticketRef) => {
    return ticketRef.update({
      isValid: false,
      validatedAt: Date.now()
    })
      .then(() => {
        setTicketUpdate("Bilet pomyślnie skasowany")
      })
      .catch((error) => {
        // The document probably doesn't exist.
        console.error("Error updating document: ", error);
        setTicketUpdate("error: " + error)
      });
  }
  //=========================================================
  const updatePromo = async (ticketRef) => {
    return ticketRef.update({
      promoValid: false,
    })
      .then(() => {
        setTicketUpdate("Promocja Pomyślne skasowana")
      })
      .catch((error) => {
        // The document probably doesn't exist.
        console.error("Error updating document: ", error);
        setTicketUpdate("error: " + error)
      });
  }
  // ========================================================
  const [scanSound] = useSound(
    scan,
    { volume: 1 }
  );
  const [errorSound] = useSound(
    error,
    { volume: 2 }
  );
  // ========================================================
  const handleDocumentFoundTicket = (ticket, ticketRef) => {
    const {
      isValid,
    } = ticket

    if (isValid) {
      updateTicket(ticketRef)
      SetScanInfo([true, "green"])
      scanSound()
    } else {
      setTicketUpdate("Bilet został juz skasowany")
      SetScanInfo([true, "red"])
      errorSound()
    }
  }
  const handleDocumentFoundPromo = (ticket, ticketRef) => {
    const {
      promoValid,
    } = ticket

    if (promoValid) {
      updatePromo(ticketRef)
      SetScanInfo([true, "green"])
      scanSound()
    } else if (promoValid === false) {
      setTicketUpdate("Promocja została już wydana")
      SetScanInfo([true, "red"])
      errorSound()
    }
    else {
      setTicketUpdate("Brak promocji w systemie")
      SetScanInfo([true, "red"])
      errorSound()
    }
  }
  // ========================================================
  const handleDocumentNotFound = (ticket, ticketRef) => {
    setClient("error")
    setTicketUpdate("Bilet nie istnieje w systemie")
    SetScanInfo([true, "red"])
    errorSound()
  }
  // ========================================================
  const handleScan = async (scannedValue) => {
    //wyszukiwanko skanera w trybie biletu
    console.log('Scanned!: ', scannedValue)
    if (scannedValue && delay && scanerMode === "tickets") {
      const timestamp = Date.now()
      setResult(scannedValue)
      setDelay(false)
      // pobieranie danych o bileciku 
      const getTicket = async () => {
        const ticketRef = await ticketsRef.doc(scannedValue)
        ticketRef.get().then((doc) => {
          if (doc.exists) {
            console.log("Document data:", doc.data());
            setClient(doc.data());
            handleDocumentFoundTicket(doc.data(), ticketRef)
          } else {
            // doc.data() will be undefined in this case
            console.log("No such document!");
            handleDocumentNotFound(doc.data(), ticketRef)
          }
        }).catch((error) => {
          console.error("Error getting document:", error);
        });
      }
      getTicket()
    }
    //wyszukiwanko skanera w trybie promocji
    else if (scannedValue && delay && scanerMode === "promos") {
      setResult(scannedValue)
      setDelay(false)
      //pobieranie info o promkach
      const getPromos = async () => {
        const ticketRef = await ticketsRef.doc(scannedValue)
        ticketRef.get().then((doc) => {
          if (doc.exists) {
            console.log("Document data:", doc.data());
            setClient(doc.data());
            handleDocumentFoundPromo(doc.data(), ticketRef)
          } else {
            // doc.data() will be undefined in this case
            console.log("No such document!");
            handleDocumentNotFound(doc.data(), ticketRef)
          }
        }).catch((error) => {
          console.error("Error getting document:", error);
        });
      }
      getPromos()
    }
  }

  // ========================================================
  const handleError = (err) => {
    console.error(err)
  }
  // ========================================================
  const makeScan = () => {
    setDelay(250)
  }
  // ========================================================
  const exitScanInfo = () => {
    setCounter(counter += 1)
    SetScanInfo([false, ""])
  }
  // ========================================================

  return (
    <div className="skaner">

      <div className="skaner_qr_reader">
        <QrReader
          delay={delay}
          onError={handleError}
          onScan={handleScan}
        />
      </div>
      <div className="skaner_options">
        {scanerMode === "tickets" ?
          <>
            <button className="skaner_options_button_1 skaner_options_picked">BILETY</button>
            <button className="skaner_options_button_2" onClick={() => setScanerMode("promos")}>PROMOCJE</button>
          </>
          :
          <>
            <button className="skaner_options_button_1" onClick={() => setScanerMode("tickets")} >BILETY</button>
            <button className="skaner_options_button_2 skaner_options_picked">PROMOCJE</button>
          </>
        }
      </div>
      <div className="skaner_scan_info">
        <div className="skaner_scan_info_scan_window">
          {scanerMode === "tickets" ? <p>QrReader</p> : <p>Promo Reader</p>}
          <p>Zeskanuj Kod by uzyskać więcej informacji</p>
          <p> Zeskanowanych w tej sesji: {counter}</p>
        </div>
      </div>

      <div className="skaner_scan_button">
        <Button variant="contained" className="skaner_scan_button_btn" style={{ borderRadius: '10px', boxShadow: '4px 4px 4px rgba(0, 0, 0, 0.5)' }} onClick={() => makeScan()}>
          SCAN
        </Button>
      </div>
      {/* =========okno skanu w absolute========== */}
      {scanerMode === "tickets" ?
        scanInfo[0] ?
          < div className={`scan_info scan_info_${scanInfo[1]}`}>
            < div className={`scan_info_box scan_info_box_${scanInfo[1]} scan_info_${scanInfo[1]}`}>
              <p className="scan_info_box_name">{Client.clientName ? Client.clientName : "no name"}</p>
              <p className="scan_info_box_code"> KOD: {result ? result : "no code"}</p>
              <p className="scan_info_box_quantity">Ilość Biletów: 1</p>
              <p className="scan_info_box_type">Rodzaj biletu: --------</p>
              <p className="scan_info_box_status">{ticketUpdate ? ticketUpdate : "error"}</p>
              <Button variant="contained" className="scan_info_button" style={{ borderRadius: '10px', boxShadow: '4px 4px 4px rgba(0, 0, 0, 0.5)', marginBottom: "2vh", width: "40vw" }} onClick={() => exitScanInfo()}>
                Ok
              </Button>
            </div >
          </div >
          : null
        :
        scanInfo[0] ?
          < div className={`scan_info scan_info_${scanInfo[1]}`}>
            < div className={`scan_info_box scan_info_box_${scanInfo[1]} scan_info_${scanInfo[1]}`}>
              <p className="scan_info_box_name">{Client.clientName ? Client.clientName : "no name"}</p>
              DO WYDAWKI:
              <p className="scan_info_box_status">{Client.drinks ? Client.drinks : "error"}</p>
              <p className="scan_info_box_status">{ticketUpdate}</p>
              <Button variant="contained" className="skaner_scan_button_btn" style={{ borderRadius: '10px', boxShadow: '4px 4px 4px rgba(0, 0, 0, 0.5)', marginBottom: "2vh", width: "40vw" }} onClick={() => exitScanInfo()}>
                Ok
              </Button>
            </div >
          </div >
          : null
      }
    </div >
  )
}

export default Scanner