import { useState, createContext, useEffect } from 'react';
import { Container } from '@material-ui/core';
import './App.scss';
import Header from './components/Header'
import Main from './components/Main'
import Footer from './components/Footer'
import DesktopWarning from './components/DesktopWarning'
import firebase from "./firebase";
import 'firebase/firestore';
import { Mobile, Desktop } from "./components/deviceDetect";
import useComponentVisible from "./components/HandleClick";

export const MenuContext = createContext()

function App() {
  const [displayed, setDisplayed] = useState("Skaner")
  const { windowref, isComponentVisible, setIsComponentVisible } =
    useComponentVisible(false, null);
  const [ticketRendered, SetTicketRendered] = useState(false);
  const [tickets, setTickets] = useState([]);

  const party = "FluoNight" // TO-DO: dodac komponent wybiernaia imprezy przy odpalaniu apki, narazie recznie
  //========WYBIERZ JEDNA POPRZEZ ZAKOMENTOWANIE===========
  // 1- nowa wersja pobierania biletow na konkretna impreze oraz ustawiania skanera na nią
  // 2 -stara wersja w przypadku gdyby nowa nie zostala zaimplementowana :) 

  //const [ticketsRef, SetTicketRef] = useState(firebase.firestore().collection("events").doc(party).collection("Tickets"))
  const [ticketsRef, SetTicketRef] = useState(firebase.firestore().collection("tickets"))


  return (
    <Container disableGutters={true}>
      <MenuContext.Provider
        value={{
          displayed,
          setDisplayed,
          windowref,
          isComponentVisible,
          setIsComponentVisible,
          ticketsRef,
          ticketRendered,
          SetTicketRendered,
          tickets,
          setTickets
        }}>

        <Mobile>
          <Header></Header>
          <Main />
          <Footer></Footer>
        </Mobile>

        <Desktop>
          <DesktopWarning />
        </Desktop>

      </MenuContext.Provider>
    </Container>
  );
}

export default App;
